.Home {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--background-primary);
  color: var(--font-primary);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Home-wrapper {
  max-width: 25rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Home-image {
  margin-bottom: -125px;
  width: 275px;
  height: auto;
}

.Home-title {
  margin-bottom: 2rem;
  text-align: center;
}

.Home-social {
  margin: 1rem 0;
}

.Home-social a {
  color: inherit;
}

.Home-social a:first-child {
  margin-right: 1rem;
}

.Home-icon {
  cursor: pointer;
  transition: color 0.4s ease;  
}

.Home-icon:hover {
  color: var(--highlight);
}

@media screen and (max-width: 799px) {
  .Home-wrapper {
    max-width: 20rem;
    margin-top: 4rem;
    margin-bottom: 2.5rem;
  }
  
  .Home-image {
    margin-bottom: -100px;
    width: 180px;
    height: auto;
  }
}

.About {
  width: 100%;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.About-wrapper {
  max-width: 36rem;
  margin-top: 14rem;
  margin-bottom: 7rem;
}

.About-tag {
  margin-left: -8rem;
  margin-bottom: 2rem;
  color: var(--highlight);
}

.About-text {
  color: var(--font-secondary);
}

.About-slides {
  margin-top: 6rem;
}

.About-slides-spacer {
  margin-bottom: 3rem;
}

.About-slide {
  max-height: 8rem;
  padding: 0 1rem;
  background-color: var(--background-secondary);
  color: var(--font-secondary);

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.About-slide img {
  width: 10rem;
  height: 10rem;
  border: 6px solid var(--font-primary);
  border-radius: 50%;
}

.About-slide-content {
  padding: 1rem;
}

.About-slide-content h4 {
  color: inherit;
  margin-bottom: 0.5rem;
}

.About-slide-content p {
  color: inherit;
  font-size: 16px;
  line-height: 20px;
}

@media screen and (max-width: 1000px) {
  .About-wrapper {
    max-width: 28rem;
  }

  .About-tag {
    margin-left: 0;
  }
}

@media screen and (max-width: 799px) {
  .About-wrapper {
    max-width: 20rem;
    margin-top: 10rem;
    margin-bottom: 5rem;
  }

  .About-tag {
    margin-bottom: 1.5rem;
  }

  .About-slides {
    margin-top: 4rem;
  }
  
  .About-slide {
    max-height: 6rem;
  }

  .About-slide img {
    width: 6.5rem;
    height: 6.5rem;
  }

  .About-slide-content {
    padding: 0.5rem;
  }

  .About-slide-content h4 {
    margin-bottom: 0.1rem;
    font-size: 16px;
  }
  
  .About-slide-content p {
    font-size: 12px;
    line-height: 16px;
  }
}

.Resume {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--background-secondary);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Resume-wrapper {
  max-width: 36rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Resume-tag {
  margin-left: -10rem;
  margin-bottom: 2rem;
  color: var(--highlight);
}

.Resume-hr {
  height: 2px;
  margin-bottom: 1rem;
  background-color: var(--highlight);
}

.Resume-spacer-vertical {
  height: 3rem;
}
  
@media screen and (max-width: 1000px) {
  .Resume-wrapper {
    max-width: 28rem;
  }

  .Resume-tag {
    margin-left: 0;
  }
}

@media screen and (max-width: 799px) {
  .Resume-wrapper {
    max-width: 20rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .Resume-tag {
    margin-bottom: 1.5rem;
  }
}

.Card {
    margin: 0;
    padding: 1rem;
    background-color: var(--background-secondary);
    color: var(--font-secondary);

    display: flex;
    flex-direction: column;
    align-items: center;
}

.Card-image {
    width: 10rem;
    height: 10rem;
    margin-top: -4rem;
    border: 6px solid var(--font-primary);
    border-radius: 50%;
}

.Card-title {
    margin-top: 2rem;
}

.Card-description {
    margin-top: 0.5rem;
    text-align: left;
    font-size: 16px;
    line-height: 20px;
}

.Projects {
  width: 100%;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Projects-wrapper {
  width: 36rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Projects-tag {
  margin-left: -10rem;
  margin-bottom: 5rem;
  color: var(--highlight);
}

.Projects-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  justify-items: center;
  align-items: start;
  grid-row-gap: 8rem;
  row-gap: 8rem;
  grid-column-gap: 4rem;
  -webkit-column-gap: 4rem;
          column-gap: 4rem;
}

@media screen and (max-width: 1000px) {
  .Projects-wrapper {
    width: 28rem;
  }

  .Projects-tag {
    margin-left: 0;
  }
}

@media screen and (max-width: 799px) {
  .Projects-wrapper {
    width: 20rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .Projects-tag {
    margin-bottom: 4rem;
  }

  .Projects-cards {
    grid-row-gap: 6rem;
    row-gap: 6rem;
  }
}

.Contact {
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: var(--background-primary);
    color: var(--font-primary);

    display: flex;
    flex-direction: column;
    align-items: center;
  }

.Contact-wrapper {
    width: 36rem;
    margin-top: 6rem;
    margin-bottom: 6rem;
}

.Contact-tag {
    margin-left: -10rem;
    margin-bottom: 2rem;
    color: var(--highlight);
}

.Contact-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    justify-items: center;
    align-items: center;
    grid-row-gap: 4rem;
    row-gap: 4rem;
}

.Contact-avatar {
    width: 12rem;
    height: 12rem;
    border: 6px solid var(--font-primary);
}

.Contact-details {
    word-break: break-all;
}

.Contact-link {
    margin-top: 0.5rem;
    color: var(--highlight);
}

.Contact-link:hover {
    box-shadow: 0 2px 0 0 var(--highlight);
    transition: box-shadow 0.4s;
}

.Contact-spacer-vertical {
    height: 1rem;
}

@media screen and (max-width: 999px) {
    .Contact-wrapper {
        width: 28rem;
    }

    .Contact-tag {
        margin-left: 0;
    }
}

@media screen and (max-width: 799px) {
    .Contact-wrapper {
        width: 20rem;
        margin-top: 4rem;
        margin-bottom: 4rem;
    }

    .Contact-tag {
        margin-bottom: 1.5rem;
    }

    .Contact-content {
        grid-row-gap: 2rem;
        row-gap: 2rem;
    }

    .Contact-avatar {
        width: 10rem;
        height: 10rem;
    }
}

.Reset {
    z-index: 200;
    position: fixed;
    bottom: 2rem;
    right: 4rem;
    cursor: pointer;

    -webkit-transform: translateX(8rem);

            transform: translateX(8rem);
    transition: -webkit-transform 0.4s ease;
    transition: transform 0.4s ease;
    transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.show-reset {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.Reset-icon {
    padding: 2px;
    color: var(--font-secondary);
    font-size: 3rem;
    transition: color 0.4s ease;
}

.Reset:hover svg {
    color: var(--highlight);
}

@media screen and (max-width: 799px) {
    .Reset {
        bottom: 1rem;
        right: 1rem;
    }

    .Reset-icon {
        font-size: 2.5rem;
    }
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
  /* color: var(--main-font-color); */
  /* background-color: var(--main-bg-color); */
}

/* CSS Vars */
:root {
  --font-primary: #ffffff;
  --font-secondary: #272f30;
  --background-primary: #5F6667;
  --background-secondary: #ededed;
  --background-dark: #383F40;
  --highlight: #DCBA6C;
  --shadow: rgb(50, 50, 50, 0.5);
}
/* END CSS Vars */

html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 0;
  padding: 0;
  font-family: "Merriweather";
  font-size: 45px;
  font-weight: 900;
  letter-spacing: 2px;
}

h2 {
  margin: 0;
  padding: 0;
  font-family: "Merriweather";
  font-size: 36px;
  font-weight: 700;
}

h3 {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
}

h4 {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

p {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

a {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
}

@media screen and (max-width: 799px) {
  h1 {
    font-size: 36px;
  }
  
  h2 {
    font-size: 22px;
  }
  
  h3 {
    font-size: 18px;
    line-height: 24px;
  }
  
  h4 {
    font-size: 16px;
    line-height: 20px;
  }
  
  p {
    font-size: 16px;
    line-height: 20px;
  }
  
  a {
    font-size: 16px;
    line-height: 20px;
  }
}

